export default function Header() {
    return (
        <nav className="flex justify-between px-10 py-5 bg-amber-200 items-center">
            <div className="flex gap-8 bg-amber-500 mx-20 items-center">
                <h1 className="text-4xl font-extrabold">D-SHOP</h1>
                <div className="flex gap-6 mx-10 text-xl bg-gray-200 items-center p-3 ">
                    <p>Shop</p>
                    <p>On Sale</p>
                    <p>New Arrivals</p>
                    <p>Brands</p>
                </div>
            </div>
            <div className="flex-grow max-w-lg mx-8 hidden md:block">
                <input
                    type="text"
                    placeholder="Search for products..."
                    className="w-full px-4 py-2 pl-10 rounded-full bg-gray-100"
                />
            </div>
            <div className="flex gap-4">
                <i class="fa-solid fa-cart-shopping"></i>
                <p>User</p>
            </div>
        </nav>
    )
}